.project {
	@include helvetica;
	will-change: transform;
	transform: perspective(1000px) rotateX(0deg) rotateY(0deg);

	width: 50%;
	max-width: calc(50% - 16px);
	margin-top: 30px;
	border-radius: 10px;
	min-height: 328px;
	@include sm {
		width: 100%;
		max-width: 100%; }

	&__link {
		color: white;
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		padding: 17px 40px 47px;
		@include md {
			padding-bottom: 27px; }
		@include sm {
			padding: 17px 22px 27px; }
		@include hover {
			.project__see {
				opacity: 1; } } }

	&__title {
		pointer-events: none; }

	&__logo {
		pointer-events: none;
		margin-top: auto; }

	&__image {}

	&__footer {
		pointer-events: none;
		margin-top: 12px;
		display: flex;
		align-items: baseline; }

	&__date {
		font-size: 20px;
		font-weight: 300; }

	&__see {
		transition: 300ms;
		margin-left: auto;
		color: white;
		text-decoration: underline;
		opacity: 0; } }
