//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

// Helvetica
@font-face {
	@include fontdefault(normal, 300, 'Helvetica');
	@include fontpath('Helvetica Light', 'Helvetica-Light', Helvetica-Light); }
@font-face {
	@include fontdefault(normal, 400, 'Helvetica');
	@include fontpath('Helvetica Regular', 'Helvetica-Regular', Helvetica-Regular); }
@font-face {
	@include fontdefault(normal, 700, 'Helvetica');
	@include fontpath('Helvetica Bold', 'Helvetica-Bold', Helvetica-Bold); }
