.footer {
	padding: 40px 0;
	@include helvetica;
	font-weight: 300;
	color: $text-dark-gray;

	&__container {
		display: flex;
		justify-content: space-between;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			height: 2px;
			top: -40px;
			left: 15px;
			right: 15px;
			background-color: #ebebeb; }

		@include md {
			flex-wrap: wrap; } }

	&__block {
		@include md {
			width: 50%; }
		@include sm {
			width: 100%;
			margin-bottom: 20px; } }

	&__block_logo {
		text-align: left; }

	&__block_address {
		text-align: left;
		@include md {
			text-align: right; }
		@include sm {
			text-align: left; } }

	&__block_contacts {
		text-align: right;
		@include md {
			margin-top: 40px;
			text-align: left; }
		@include sm {
			margin-top: 0; } }

	&__logo {}

	&__logo-image {}

	&__feedback-block {
		display: flex;
		align-items: center;
		justify-content: flex-end;
		.icon {
			margin-right: 9px; }
		@include md {
			justify-content: flex-start; } }

	&__text {
		@include md {
			font-size: 14px; } }

	&__text_sm {
		font-size: 14px; }

	&__text_bold {
		font-weight: 700; }

	&__text_underline {}

	&__link {
		display: block;
		color: $text-dark-gray;
		@include hover {
			text-decoration: underline; } } }
