html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 300;
  src: local("Helvetica Light"), local("Helvetica-Light"), url("../fonts/Helvetica-Light.woff2") format("woff2"), url("../fonts/Helvetica-Light.woff") format("woff");
}

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  src: local("Helvetica Regular"), local("Helvetica-Regular"), url("../fonts/Helvetica-Regular.woff2") format("woff2"), url("../fonts/Helvetica-Regular.woff") format("woff");
}

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  src: local("Helvetica Bold"), local("Helvetica-Bold"), url("../fonts/Helvetica-Bold.woff2") format("woff2"), url("../fonts/Helvetica-Bold.woff") format("woff");
}

.icon-pen {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-phone {
  width: 1em;
  height: 1em;
  fill: currentColor;
}

body {
  background: #FFF;
  line-height: 1.4;
  font-size: 16px;
  min-width: 320px;
}

a {
  text-decoration: none;
}

.container {
  padding: 0 15px;
  width: 100%;
  max-width: 1272px;
  margin: 0 auto;
}

html {
  background-color: #1f1f1f;
}

.nav__list {
  min-width: 410px;
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 1023px) {
  .nav__list {
    min-width: 310px;
  }
}

@media only screen and (max-width: 767px) {
  .nav__list {
    min-width: auto;
    width: 100%;
  }
}

.nav__link {
  color: black;
}

.no-touch .nav__link:hover {
  text-decoration: underline;
}

@media only screen and (max-width: 767px) {
  .nav__link {
    letter-spacing: -0.4px;
  }
}

.button {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  transition: 0.3s;
  display: inline-block;
  font-weight: 700;
  background: transparent;
  border: 1px solid #fff;
  border-radius: 4px;
  font-size: 16px;
  color: #fff;
  padding: 12px 70px;
}

.no-touch .button:hover {
  color: #adadad;
  background-color: rgba(0, 0, 0, 0.3);
}

.button_uppercase {
  text-transform: uppercase;
}

.button_purple {
  color: #fff;
  background-color: #9013fe;
  border: 1px solid #d8d8d8;
}

.no-touch .button_purple:hover {
  color: #ededed;
  background-color: #6116a3;
}

.title {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-size: 42px;
  font-weight: 700;
  color: #000;
  position: relative;
}

.title_white {
  color: #fff;
}

.title_decorated {
  padding-bottom: 18px;
}

.title_decorated:after {
  content: '';
  position: absolute;
  width: 54px;
  height: 8px;
  background-color: #000;
  left: 0;
  bottom: 0;
}

.title_sm {
  font-size: 22px;
}

.process {
  overflow: hidden;
  padding: 23px 40px;
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  width: 50%;
  max-width: calc(50% - 16px);
  border-radius: 10px;
  margin-top: 30px;
  position: relative;
}

.process:before {
  transition: 300ms;
  content: '';
  width: 100%;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #16052c;
}

@media only screen and (max-width: 767px) {
  .process {
    width: 100%;
    max-width: none;
    padding: 8px 12px;
  }
}

.no-touch .process:hover {
  color: #fff;
}

.no-touch .process:hover:before {
  height: 100%;
}

.no-touch .process:hover .process__head:after {
  max-width: 100%;
  background-color: #fff;
}

.no-touch .process:hover .process__count {
  color: #fff;
}

.no-touch .process:hover .process__title {
  color: white;
}

.no-touch .process:hover .process__descr {
  color: #e6e6e6;
}

.process__head {
  position: relative;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding-bottom: 13px;
}

.process__head:after {
  transition: 300ms;
  content: '';
  position: absolute;
  width: 100%;
  max-width: 56px;
  height: 5px;
  background-color: #9013fe;
  left: 0;
  bottom: 0;
}

@media only screen and (max-width: 767px) {
  .process__head:after {
    max-width: 36px;
  }
}

.process__count {
  transition: 300ms;
  position: relative;
  font-size: 50px;
  font-weight: 700;
  color: #9013fe;
}

@media only screen and (max-width: 767px) {
  .process__count {
    font-size: 32px;
  }
}

.process__title {
  transition: 300ms;
  margin-left: 18px;
}

.process__descr {
  transition: 300ms;
  position: relative;
  color: #646464;
}

.project {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  will-change: transform;
  transform: perspective(1000px) rotateX(0deg) rotateY(0deg);
  width: 50%;
  max-width: calc(50% - 16px);
  margin-top: 30px;
  border-radius: 10px;
  min-height: 328px;
}

@media only screen and (max-width: 767px) {
  .project {
    width: 100%;
    max-width: 100%;
  }
}

.project__link {
  color: white;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 17px 40px 47px;
}

@media only screen and (max-width: 1023px) {
  .project__link {
    padding-bottom: 27px;
  }
}

@media only screen and (max-width: 767px) {
  .project__link {
    padding: 17px 22px 27px;
  }
}

.no-touch .project__link:hover .project__see {
  opacity: 1;
}

.project__title {
  pointer-events: none;
}

.project__logo {
  pointer-events: none;
  margin-top: auto;
}

.project__footer {
  pointer-events: none;
  margin-top: 12px;
  display: flex;
  align-items: baseline;
}

.project__date {
  font-size: 20px;
  font-weight: 300;
}

.project__see {
  transition: 300ms;
  margin-left: auto;
  color: white;
  text-decoration: underline;
  opacity: 0;
}

.header {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  background-color: #fff;
  font-weight: 700;
}

@media only screen and (max-width: 1023px) {
  .header {
    font-size: 16px;
  }
}

.header__container {
  display: flex;
  align-items: center;
  min-height: 65px;
}

@media only screen and (max-width: 767px) {
  .header__container {
    justify-content: center;
    flex-wrap: wrap;
    padding: 15px;
  }
}

.header__nav {
  margin-left: 60px;
}

@media only screen and (max-width: 1023px) {
  .header__nav {
    margin-left: 20px;
  }
}

@media only screen and (max-width: 767px) {
  .header__nav {
    max-width: 480px;
    margin-top: 12px;
    margin-left: 0;
    width: 100%;
    order: 1;
  }
}

.header__feedback {
  display: flex;
  justify-content: space-between;
  margin-left: auto;
  min-width: 324px;
}

@media only screen and (max-width: 1023px) {
  .header__feedback {
    min-width: 280px;
  }
}

@media only screen and (max-width: 767px) {
  .header__feedback {
    min-width: auto;
    display: flex;
    flex-direction: column;
  }
}

.header__link {
  font-size: 15px;
  display: flex;
  align-items: center;
  color: black;
}

.no-touch .header__link:hover {
  text-decoration: underline;
}

.header__link .icon {
  margin-right: 9px;
}

@media only screen and (max-width: 767px) {
  .header__link {
    margin-left: 24px;
  }
}

.main-hero {
  overflow: hidden;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  color: #fff;
  position: relative;
  background: url("../img/hero-background.jpg") center no-repeat;
  background-size: cover;
}

.main-hero:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-image: linear-gradient(42deg, rgba(31, 2, 64, 0.9) 0%, rgba(91, 57, 152, 0.9) 100%);
}

.main-hero__container {
  position: relative;
  min-height: 694px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

.main-hero__container:after {
  content: '';
  pointer-events: none;
  position: absolute;
  background: url("../img/iphone-mac.png") no-repeat 815px;
  top: 0;
  left: 0;
  width: 200%;
  height: 100%;
}

.main-hero__descr {
  margin-top: 18px;
}

.main-hero__table {
  max-width: 524px;
  margin-top: 24px;
}

.main-hero__table-data {
  text-align: center;
  font-size: 32px;
  font-weight: 700;
}

.main-hero__table-head {
  text-align: center;
  font-weight: 300;
  font-size: 14px;
  text-transform: uppercase;
}

.main-hero__button {
  margin-top: 54px;
}

.processes {
  padding-top: 50px;
  padding-bottom: 50px;
}

.processes__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.portfolio-list {
  background-color: #fafafa;
  padding-top: 37px;
  padding-bottom: 60px;
}

.portfolio-list__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.cta {
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  padding-top: 60px;
  padding-bottom: 80px;
}

.cta__descr {
  margin-top: 20px;
  color: #4d4d4d;
  font-weight: 700;
  line-height: 2.5;
}

@media only screen and (max-width: 1023px) {
  .cta__descr {
    line-height: 1.4;
  }
}

.cta__subtitle {
  margin-top: 14px;
}

.cta__cta {
  margin-top: 24px;
  color: #9013fe;
  font-size: 32px;
  font-weight: 700;
}

.cta__form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.cta__input {
  font-size: 18px;
  margin-top: 7px;
  min-height: 64px;
  max-width: 494px;
  width: 100%;
  border: 1px solid #d8d8d8;
  border-radius: 5px;
  padding: 24px;
}

.cta__input::placeholder {
  font-size: 14px;
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  color: #6c6c6c;
}

.cta__input:focus {
  border-color: #9013fe;
}

.cta__textarea {
  resize: none;
}

.cta__file-label {
  margin-top: 30px;
  max-width: 494px;
  width: 100%;
  cursor: pointer;
  display: flex;
  align-items: center;
  color: #353535;
  text-decoration: underline;
}

.cta__file-label:before {
  flex-shrink: 0;
  content: '';
  display: block;
  width: 30px;
  height: 38px;
  background: url("../img/input-file.svg") no-repeat center;
  margin-right: 20px;
}

.no-touch .cta__file-label:hover {
  text-decoration: none;
  color: rgba(53, 53, 53, 0.7);
}

.cta__input-file {
  display: none;
}

.cta__policy {
  margin-top: 20px;
  font-size: 14px;
  font-weight: 300;
  color: #4d4d4d;
}

.cta__link {
  color: #4d4d4d;
}

.no-touch .cta__link:hover {
  text-decoration: underline;
}

.cta__button {
  margin-top: 12px;
}

.footer {
  padding: 40px 0;
  font-family: 'Helvetica', 'Roboto', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
  font-weight: 300;
  color: #4d4d4d;
}

.footer__container {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.footer__container:before {
  content: '';
  position: absolute;
  height: 2px;
  top: -40px;
  left: 15px;
  right: 15px;
  background-color: #ebebeb;
}

@media only screen and (max-width: 1023px) {
  .footer__container {
    flex-wrap: wrap;
  }
}

@media only screen and (max-width: 1023px) {
  .footer__block {
    width: 50%;
  }
}

@media only screen and (max-width: 767px) {
  .footer__block {
    width: 100%;
    margin-bottom: 20px;
  }
}

.footer__block_logo {
  text-align: left;
}

.footer__block_address {
  text-align: left;
}

@media only screen and (max-width: 1023px) {
  .footer__block_address {
    text-align: right;
  }
}

@media only screen and (max-width: 767px) {
  .footer__block_address {
    text-align: left;
  }
}

.footer__block_contacts {
  text-align: right;
}

@media only screen and (max-width: 1023px) {
  .footer__block_contacts {
    margin-top: 40px;
    text-align: left;
  }
}

@media only screen and (max-width: 767px) {
  .footer__block_contacts {
    margin-top: 0;
  }
}

.footer__feedback-block {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.footer__feedback-block .icon {
  margin-right: 9px;
}

@media only screen and (max-width: 1023px) {
  .footer__feedback-block {
    justify-content: flex-start;
  }
}

@media only screen and (max-width: 1023px) {
  .footer__text {
    font-size: 14px;
  }
}

.footer__text_sm {
  font-size: 14px;
}

.footer__text_bold {
  font-weight: 700;
}

.footer__link {
  display: block;
  color: #4d4d4d;
}

.no-touch .footer__link:hover {
  text-decoration: underline;
}
