.nav {

	&__list {
		min-width: 410px;
		display: flex;
		justify-content: space-between;
		@include md {
			min-width: 310px; }
		@include sm {
			min-width: auto;
			width: 100%; } }

	&__item {}

	&__link {
		color: black;
		@include hover {
			text-decoration: underline; }
		@include sm {
			letter-spacing: -0.4px; } } }
