.button {
	@include helvetica;
	transition: 0.3s;
	display: inline-block;
	font-weight: 700;
	background: transparent;
	border: 1px solid $white;
	border-radius: 4px;
	font-size: 16px;
	color: $white;
	padding: 12px 70px;
	@include hover {
		color: $gray;
		background-color: rgba($black, .3); }

	&_uppercase {
		text-transform: uppercase; }

	&_purple {
		color: $white;
		background-color: $purple;
		border: 1px solid #d8d8d8;
		@include hover {
			color: #ededed;
			background-color: #6116a3; } } }
